<template>
    <el-card class="box-card card-form">
        <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" class="demo-ruleForm">
            <el-form-item label="Username" prop="email">
                <el-input type="Text" v-model="ruleForm.email" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="Password" prop="password">
                <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">Submit</el-button>
                <el-button @click="resetForm('ruleForm')">Reset</el-button>
            </el-form-item>
        </el-form>
    </el-card>
</template>

<script>

import moduleAuth from '../store/auth/moduleAuth.js';

export default {
    data() {
      var validateEmail = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('Please input the Username'));
        } else {
          callback();
        }
      };
      var validatePassword = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('Please input the password again'));
        } else if (value !== this.ruleForm.pass) {
          callback(new Error('Two inputs don\'t match!'));
        } else {
          callback();
        }
      };
      return {
        ruleForm: {
          email : '',
          password: '',
        },
        rules: {
          username: [
            { validator: validateEmail, trigger: 'blur' }
          ],
          pass: [
            { validator: validatePassword, trigger: 'blur' }
          ],
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {

            this.$store.dispatch('dataAuth/loginAuth', this.ruleForm)
            
            // alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    },
    created () {
    if (!moduleAuth.isRegistered) {
      this.$store.registerModule('dataAuth', moduleAuth)
      moduleAuth.isRegistered = true
    }
  },
  }
</script>

<style>

.card-form {
    margin: 0 auto;
    margin-top: 150px;
    width: 320px;
}
</style>